.transform{
  --tw-translate-x:0;
  --tw-translate-y:0;
  --tw-rotate:0;
  --tw-skew-x:0;
  --tw-skew-y:0;
  --tw-scale-x:1;
  --tw-scale-y:1;
  transform:translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y))
}

.transition{
  transition-property:background-color, border-color, color, fill, stroke, opacity, box-shadow, transform;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

@-webkit-keyframes spin{
  to{
    transform:rotate(360deg)
  }
}

@keyframes spin{
  to{
    transform:rotate(360deg)
  }
}

@-webkit-keyframes ping{
  75%, 100%{
    transform:scale(2);
    opacity:0
  }
}

@keyframes ping{
  75%, 100%{
    transform:scale(2);
    opacity:0
  }
}

@-webkit-keyframes pulse{
  50%{
    opacity:.5
  }
}

@keyframes pulse{
  50%{
    opacity:.5
  }
}

@-webkit-keyframes bounce{
  0%, 100%{
    transform:translateY(-25%);
    -webkit-animation-timing-function:cubic-bezier(0.8,0,1,1);
            animation-timing-function:cubic-bezier(0.8,0,1,1)
  }

  50%{
    transform:none;
    -webkit-animation-timing-function:cubic-bezier(0,0,0.2,1);
            animation-timing-function:cubic-bezier(0,0,0.2,1)
  }
}

@keyframes bounce{
  0%, 100%{
    transform:translateY(-25%);
    -webkit-animation-timing-function:cubic-bezier(0.8,0,1,1);
            animation-timing-function:cubic-bezier(0.8,0,1,1)
  }

  50%{
    transform:none;
    -webkit-animation-timing-function:cubic-bezier(0,0,0.2,1);
            animation-timing-function:cubic-bezier(0,0,0.2,1)
  }
}

body{
  background:#fff
}

[data-whatintent='mouse'] *:focus{
  outline:none
}

html,body{
  height:100%
}

body{
  display:flex;
  flex-direction:column
}

body>main{
  flex:1 0 auto
}

body>footer{
  flex-shrink:0
}

[data-aspectRatio="1:1"]{
  --aspect-ratio: 1
}

[data-aspectRatio="4:3"]{
  --aspect-ratio: .75
}

[data-aspectRatio]{
  position:relative
}

[data-aspectRatio]>:first-child{
  width:100%;
  position:absolute;
  top:0;
  left:0;
  height:100%
}

[data-aspectRatio]>img{
  height:auto
}

[data-aspectRatio]::before{
  content:"";
  display:block;
  padding-bottom:calc(100% / (var(--aspect-ratio)))
}

.title-bar .title-bar-inner{
  display:flex;
  flex-direction:row;
  align-content:center;
  justify-content:center;
  margin-left:auto;
  margin-right:auto;
  padding-top:0.5rem;
  padding-bottom:0.5rem;
  padding-left:0.5rem;
  padding-right:0.5rem
}

.title-bar .logo-wrap{
  flex:1 1;
  text-align:center
}

.title-bar .menu-icon{
  cursor:pointer
}

@media (min-width: 768px){
  .title-bar{
    display:none
  }
}

.top-bar-inner{
  display:none;
  margin-left:auto;
  margin-right:auto;
  width:100%
}

@media (min-width: 768px){
  .top-bar-inner{
    display:block
  }
}

@media screen and (max-width: 768px){
  #top-bar-open:checked ~ .top-bar-inner{
    display:block;
    height:100%
  }

  #top-bar-open:checked ~ .top-bar-inner .main-menu{
    grid-auto-flow:row;
    padding-left:1.5rem;
    padding-right:1.5rem
  }

  #top-bar-open:checked ~ .top-bar-inner .top-bar-ceil, #top-bar-open:checked ~ .top-bar-inner .top-bar-floor, #top-bar-open:checked ~ .top-bar-inner .logo-wrap{
    display:none
  }

  #top-bar-open:checked ~ .top-bar-inner .main-menu-container{
    width:100%
  }

  #top-bar-open:checked ~ .top-bar-inner .menu-item-has-children>a{
    width:100%;
    display:flex;
    flex-direction:row;
    font-size:1.25rem;
    line-height:1.75rem;
    border-bottom-width:1px
  }

  #top-bar-open:checked ~ .top-bar-inner .menu-item-has-children>a .title-wrap{
    flex:1 1
  }

  #top-bar-open:checked ~ .top-bar-inner .menu-item-has-children>a .submenu-icon{
    display:block;
    align-self:flex-end;
    transform:rotate(-90deg)
  }
}

.top-bar-ceil{
  padding-top:1.5rem;
  padding-bottom:1.5rem;
  display:flex;
  flex-direction:row;
  vertical-align:baseline;
  margin-left:auto;
  margin-right:auto
}

.top-bar-ceil .switcher{
  list-style-type:none;
  display:flex;
  flex-direction:row;
  flex:1 1
}

.top-bar-ceil .switcher>li{
