}

.top-bar-ceil .switcher>li>a{
  padding-bottom:0.5rem;
  --tw-border-opacity:1;
  border-color:rgba(0, 0, 0, var(--tw-border-opacity));
  border-bottom-width:4px
}

.top-bar-ceil .switcher>li>a.active{
  --tw-text-opacity:1;
  color:rgba(61, 148, 226, var(--tw-text-opacity));
  --tw-border-opacity:1;
  border-color:rgba(61, 148, 226, var(--tw-border-opacity))
}

.top-bar-center{
  display:flex;
  flex-direction:row;
  margin-left:auto;
  margin-right:auto;
  margin-bottom:1rem
}

.top-bar-center .logo-wrap{
  flex:1 1
}

.top-bar-center .logo-wrap>.logo{
  font-weight:700;
  font-size:1.875rem;
  line-height:2.25rem;
  --tw-text-opacity:1;
  color:rgba(0, 0, 0, var(--tw-text-opacity))
}

.main-menu-container .submenu-icon{
  display:none
}

@media (min-width: 768px){
  .main-menu-container{
    align-self:flex-end;
    padding-bottom:1rem
  }

  .main-menu-container ul{
    display:grid;
    list-style-type:none;
    margin:0px;
    padding:0px
  }

  .main-menu-container ul li{
    padding:0px
  }

  .main-menu-container li{
    display:block;
    cursor:pointer
  }

  .main-menu-container .main-menu{
    grid-auto-flow:column;
    grid-column-gap:1.5rem;
    -webkit-column-gap:1.5rem;
            column-gap:1.5rem
  }

  .main-menu-container .main-menu>li>a{
    text-transform:uppercase
  }

  .main-menu-container .top-bar-ceil, .main-menu-container .top-bar-floor, .main-menu-container .logo-wrap{
    display:block
  }

  .main-menu-container .menu-item-has-children{
    position:relative
  }

  .main-menu-container .menu-item-has-children .submenu-icon{
    display:inline-block;
    margin-left:0.5rem;
    transition:280ms all 120ms ease-out
  }

  .main-menu-container .menu-item-has-children .sub-menu{
    position:absolute;
    min-width:16rem;
    left:50%;
    top:calc(100% - 0.25rem);
    transition:280ms all 120ms ease-out;
    transform:rotateX(-90deg) translateX(-50%);
    transform-origin:top center;
    visibility:hidden;
    opacity:0.3;
    background-color:#F2F2F2;
    z-index:10;
    --tw-shadow:0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    box-shadow:0 0 #0000, 0 0 #0000, var(--tw-shadow);
    box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    margin-top:0.5rem;
    --tw-border-opacity:1;
    border-color:rgba(61, 148, 226, var(--tw-border-opacity));
    border-top-width:4px
  }

  .main-menu-container .menu-item-has-children .sub-menu li>a{
    padding-top:1rem;
    padding-bottom:1rem;
    padding-left:1rem;
    padding-right:1rem;
    width:100%;
    display:block
  }

  .main-menu-container .menu-item-has-children .sub-menu li>a .title-wrap{
    --tw-text-opacity:1;
    color:rgba(61, 148, 226, var(--tw-text-opacity));
    text-decoration:underline
  }

  .main-menu-container .menu-item-has-children .sub-menu li>a:hover{
    --tw-bg-opacity:1;
    background-color:rgba(255, 255, 255, var(--tw-bg-opacity))
  }

  .main-menu-container .menu-item-has-children .sub-menu .subtitle{
    margin-top:0.5rem;
    font-size:0.875rem;
    line-height:1.25rem;
    --tw-text-opacity:1;
    color:rgba(0, 0, 0, var(--tw-text-opacity))
  }

  .main-menu-container .menu-item-has-children:hover .submenu-icon,.main-menu-container .menu-item-has-children:focus-within .submenu-icon{
    transform:rotate(90deg)
  }

  .main-menu-container .menu-item-has-children:hover .sub-menu,.main-menu-container .menu-item-has-children:focus-within .sub-menu{
    opacity:1;
    transform:rotateX(0) translateX(-50%);
    visibility:visible
  }

  .main-menu-container .menu-item-has-children:hover .sub-menu a,.main-menu-container .menu-item-has-children:focus-within .sub-menu a{
    opacity:1
  }

  .main-menu-container .menu-item-has-children:hover:after,.main-menu-container .menu-item-has-children:focus-within:after{
    opacity:1
  }
}

